.cart-total {
    background-color: rgb(248, 249, 250);
    & h5 {
        font-weight: 500;
    }
    &__coupon {
        & .input {
            padding: 0.8rem 1.5rem;
        }
    }
}

.apply-coupon {
    display: block;
    width: 100%;
    &:hover {
        border-color: black;
    }
}

.cart-nav {
    & .btn {
        background-color: transparent !important;
        padding-left: 20px;

        &:hover {
            box-shadow: none;
            border-color: rgb(33, 37, 41);
        }

        &:hover::before {
            display: none;
        }
    }
}