.title {
    text-transform: uppercase;
    & p {
        margin-bottom: 4px;
        color: #999;
    }
    & h2 {
        font-size: 2.2rem;
        font-weight: 500;
    }

    &--center {
        text-align: center;
    }
    &--left {
        text-align: left;
    }
    &--right {
        text-align: right;
    }
}