.cart-manager {
    & .product-img {
        width: 100px;
        height: 100px;
    }

    & .quantity {
        border: none !important;
        min-width: 100px;
    }
}

.btn--micro {
    &:hover {
        box-shadow: none !important;
        &::before {
            display: none;
        }
    }
}